/** @jsxImportSource preact */
import { h, render } from 'preact';
import { PluginSettingTab, Notice } from 'obsidian';
import ThinkPlugin from '../main';
import { DashboardConfigForm } from './DashboardConfigForm';

export class SettingsTab extends PluginSettingTab {
  private plugin: ThinkPlugin;

  constructor(app: any, plugin: ThinkPlugin) {
    super(app, plugin);
    this.plugin = plugin;
  }
  /* ------------------------------------------------------------------ */
  /** Âà∑Êñ∞Êï¥‰∏™ËÆæÁΩÆÈù¢Êùø */
  display(): void {
    const { containerEl } = this;
    containerEl.empty();
    containerEl.createEl('h2', { text: 'Think ‰ª™Ë°®Áõò - ÈÖçÁΩÆÁÆ°ÁêÜ' });

    /* ‚îÄ‚îÄ Êñ∞Âª∫‰ª™Ë°®Áõò ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
    containerEl
      .createEl('button', { text: '‚ûï Êñ∞Âª∫‰ª™Ë°®Áõò', cls: 'mod-cta' })
      .onclick = () => {
        let name = 'Êñ∞‰ª™Ë°®Áõò', n = 1;
        while (this.plugin.dashboards.some(d => d.name === name))
          name = `Êñ∞‰ª™Ë°®Áõò${n++}`;

        this.plugin.dashboards.push({ name, modules: [] });
        this.persistAndReload('Â∑≤ÂàõÂª∫Êñ∞‰ª™Ë°®Áõò');
      };

    /* ‚îÄ‚îÄ Â±ïÂºÄÊ≠§ÂâçÁÇπÂáªÁöÑ‰ª™Ë°®ÁõòÔºàlocalStorage ËÆ∞ÂøÜÔºâ ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
    const wantOpen = localStorage.getItem('think-target-dash');

    /* ‚îÄ‚îÄ ÂàóÂá∫ÊâÄÊúâ‰ª™Ë°®Áõò ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
    this.plugin.dashboards.forEach((dash, idx) => {
      const details = containerEl.createEl('details', { cls: 'think-settings-block' });
      if (dash.name === wantOpen) {
        details.open = true;
        localStorage.removeItem('think-target-dash');
      }

      /* summary Âå∫ÔºàÂêçÁß∞ + Âà†Èô§ÊåâÈíÆÔºâ */
      const summary = details.createEl('summary');
      summary.addClass('setting-item');
      summary.createSpan({ text: dash.name });

      /* Âà†Èô§ */
      summary.createEl('button', { text: 'üóë', cls: 'mod-warning' }).onclick = e => {
        e.stopPropagation();
        if (confirm(`Á°ÆËÆ§Âà†Èô§‰ª™Ë°®Áõò„Äå${dash.name}„ÄçÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊí§ÈîÄÔºÅ`)) {
          this.plugin.dashboards.splice(idx, 1);
          this.persistAndReload('Â∑≤Âà†Èô§‰ª™Ë°®Áõò');
        }
      };

      /* Ë°®ÂçïÊåÇËΩΩÁÇπ */
      const host = details.createDiv();

      /* Ê∏≤Êüì DashboardConfigFormÔºà‰º†ÂÖ•ÂÖãÈöÜ‰ª•ÈÅøÂÖçÂç≥Êó∂‰øÆÊîπÂéüÊï∞ÊçÆÔºâ */
      render(
        h(DashboardConfigForm, {
          dashboard  : structuredClone(dash),
          dashboards : this.plugin.dashboards,
          /* ‰øùÂ≠òÔºöÂÜôÂõû dash ‚Üí persist ‚Üí Âà∑Êñ∞ UI */
          onSave     : (d) => {
            Object.assign(dash, d);
            this.persistAndReload('Â∑≤‰øùÂ≠ò');
            /* ‰øùÂ≠òÂêé‰øùÊåÅÂ±ïÂºÄÁä∂ÊÄÅ */
            localStorage.setItem('think-target-dash', dash.name);
          },
          /* ÂèñÊ∂àÔºöÁÆÄÂçïÂà∑Êñ∞‰∏ÄÊ¨°Âç≥ÂèØÊÅ¢Â§çÂà∂ÂâçÁä∂ÊÄÅ */
          onCancel   : () => this.display(),
        }),
        host,
      );
    });
  }

  /* ------------------------------------------------------------------ */
  /** ÊåÅ‰πÖÂåñÂÖ®ÈÉ® dashboardsÔºåÂπ∂Âà∑Êñ∞Èù¢Êùø */
  private persistAndReload(msg: string) {
    this.plugin.persistAll().then(() => {
      new Notice(msg);
      this.display();
    });
  }
}